namespace $.$$ {
// ============================================================================

export const $nl_login_input = <$me_atom2_elem_cnf_intf>{
  prop: {
    placeholder: $me_atom2_prop_abstract<string>(),
    value: $me_atom2_prop_abstract<string>(),

    icon_width: () => 0,
    icon: () => '',
    type: () => 'text',
    icon_wrapper_width: $me_atom2_prop(['.icon_width', '.icon_margin_right'],
      ({masters: [icon_width, icon_margin_right]}) => icon_width + 2 * icon_margin_right
    ),
    fontSize: () => 18,
    icon_height: () => 18,
    icon_margin_right: () => 8,
    icon_isClickable: () => false,
    error: () => '',
    '#height': () => 45,
  },
  elem: {
    input: () => <$me_atom2_elem_cnf_intf>{
      base: $nl_input,
      prop: {
        placeholder: '<.placeholder',
      },
      attr: {
        type: '<.type',
      },
      style: {
        fontSize: '<.fontSize',
        border: $me_atom2_prop(['<.error'],
          ({masters: [error]}) =>
            error ? 'solid 1px #ff3939' : '',
        ),
      },
      dom: {
        value: '<.value',
      },
      dispatch: (dispatch_name: string, dispatch_arg: any) => {
        if (dispatch_name == 'change') {
          a<string>('<.value', dispatch_arg)
          return true
        } else if (
          dispatch_name == 'keypress' ||
          dispatch_name == 'keydown' && (
            dispatch_arg.key == 'Delete' ||
            dispatch_arg.key == 'Backspace'
          )
        ) {
          a<string>('<.error', '')
          return true
        }
        return false
      },
    },

    icon_wrapper: $me_atom2_prop(['.icon_width'],
      ({masters: [icon_width]}) => !icon_width ? null :
      <$me_atom2_elem_cnf_intf>{
        prop: {
          '#alignHor': () => $me_align.right,
          '#width': '<.icon_wrapper_width',
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 2,
          ),
          '#cursor': $me_atom2_prop(['<.icon_isClickable'],
            ({masters: [icon_isClickable]}) =>
              !icon_isClickable ? null : 'pointer',
          ),
        },
        elem: {
          icon: $me_atom2_prop(['<.icon'],
            ({masters: [icon]}) => {
              return !icon ? null : <$me_atom2_elem_cnf_intf>{
                type: icon.type,
                base: icon,
                prop: {
                  '#alignHor': () => $me_align.right,
                  '#ofsHor': '<<.icon_margin_right',
                  '#alignVer': () => $me_align.center,
                  '#width': '<<.icon_width',
                  '#height': '<<.icon_height',
                },
              }
            }
          ),
        },
        event: {
          clickOrTap: () => {
            if (a<boolean>('<.icon_isClickable')) {
              a.dispatch('<', 'icon_click')
              return true
            }
            return false
          },
        },
      },
    ),

    error: $me_atom2_prop(['.error'],
      ({masters: [error]}) => !error ? null :
        <$me_atom2_elem_cnf_intf>{
          base: $nl_login_error,
          prop: {
            caption: '<.error',
          },
        }
    ),

  },
}

// ============================================================================

const $nl_login_error = <$me_atom2_elem_cnf_intf>{
  prop: {
    caption: '<.error',
    paddingHor: () => 16,
    width: $me_atom2_prop(['.triangle_width', '.paddingHor', '@caption.#width', '.paddingHor'],
      $me_atom2_prop_compute_fn_sum(),
    ),


    triangle_width: () => 16,
    '#width': $me_atom2_prop(['.width', '.triangle_width'],
      $me_atom2_prop_compute_fn_diff(),
    ),
    colorBackground: () => '#ff3637',
    colorText: () => 'white',
    '#alignHor': () => $me_align.right,
    '#ofsHor': $me_atom2_prop(['.width'],
      ({masters: [width]}) => -width - 4,
    ),
  },
  style: {
    background: '.colorBackground',
  },

  elem: {
    triangle: () => <$me_atom2_elem_cnf_intf>{
      base: $me_triangle,
      prop: {
        '#ofsHor': $me_atom2_prop(['.#width'],
          ({masters: [width]}) => -width,
        ),
        direction: () => $me_pos_enum.left,
        size: '.em',
        // '#height': '<.#height',
        color: '<.colorBackground',
      },
    },
    caption: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => null as number,
        '#height': () => null as number,
        '#align': () => $me_align.center,
      },
      dom: {
        innerText: '<.caption',
      },
      style: {
        color: '<.colorText',
      },
    },
  },
}

// ============================================================================
}
