namespace $.$$ {
// ============================================================================

/*
/@app@stations@crumbs^canvas.height_visible_manual  56
/@app@stations.ofsVer  88
/@app@stations^canvas.height_visible_manual  32
*/

export interface $nl_phones_crumb_intf {
  guid: string,
  text: string,
  navText?: string,
  navFrom?: number,
  ctxLeft?: number,
  ctxTop?: number,
  ctxWidth?: number,
  ctxLeftHor?: number,
}

export type $nl_phones_crumbs_type = Array<$nl_phones_crumb_intf>

export const $nl_phones = <$me_atom2_elem_cnf_intf>{
base: $nl_phones_crumbs,
  prop: {
    isTouch: $me_atom2_prop_store({
      default: () => a('/.#isTouch'),
      valid: (val: any) => typeof val == 'boolean' ? val : null,
    }),
    '#height': '<.#height',
    '#width': '<.#width',
    crumb_height: $me_atom2_prop_either(['.isTouch'],
      () => 24,//32,
      () => 24,
    ),
    fontSize: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 14,
    ),
    crumbSpaceHor: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbSpaceVer: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbBorderRadius: $me_atom2_prop_either(['.isTouch'],
      () => 5,
      () => 3,
    ),
    iconScale: $me_atom2_prop_either(['.isTouch'],
      () => .7,
      () => .7 * 24 / 32,
    ),
    iconMarginLeft: $me_atom2_prop_either(['.isTouch'],
      () => 0,//6,
      () => 0,//4,
    ),
    iconMarginRight: $me_atom2_prop_either(['.isTouch'],
      () =>0,// 6,
      () =>0,// 4,
    ),
    crossMargin: $me_atom2_prop_either(['.isTouch'],
      () => 0,// 22 + 1,
      () => 0,// 16,
    ),
    // '#zIndex': $me_atom2_prop(['<.#zIndex'],
    //   ({masters: [zIndex]}) => zIndex + 1,
    // ),

    data: () => <object[]>[],

    crumbs: $me_atom2_prop<$nl_stations_crumbs_type>(['.data'],
      ({masters: [data]}) => {
        const result = [] as $nl_stations_crumbs_type
        const arr = data.split(',')
        for (const obj of arr) {
          const phone = obj
          const caption = $nl_formatter_phone(phone)
          result.push(<$nl_stations_crumb_intf>{
            guid: phone,
            text: caption,
          })
        }
        return result
      },
    ),
  },
}

// ============================================================================
}