          //   )
          // }
          // }
          // if (a<boolean>('.isResizeMode')) {
          //   $me_atom2_wheel_touch.cancel()
          //   // $me_atom2_ec_body_cursor({origin: a.curr.path, val: 'row-resize'})
          //   const clientRect = a<$me_rect_intf>('.#clientRect')
          //   a('.height_visible_manual',
          //     Math.min(
