export interface $nl_stations_crumb_intf {
  guid: string,
  code: string,
  text: string,
  walking_access: boolean,
  transport_access: boolean,
  color: string,
  navText?: string,
  navFrom?: number,
  ctxLeft?: number,
  ctxTop?: number,
  ctxWidth?: number,
  ctxLeftHor?: number,

  deleteProgress?: number,
  deleteStart?: number
}

export type $nl_stations_crumbs_type = Array<$nl_stations_crumb_intf>

export const $nl_stations = <$me_atom2_elem_cnf_intf>{
base: $nl_stations_crumbs,
  prop: {
    isTouch: $me_atom2_prop_store({
      default: () => a('/.#isTouch'),
      valid: (val: any) => typeof val == 'boolean' ? val : null,
    }),
    '#height': '<.#height',
    '#width': '<.#width',
    crumb_height: $me_atom2_prop_either(['.isTouch'],
      () => 24,//32,
      () => 24,
    ),
    fontSize: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 14,
    ),
    crumbSpaceHor: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbSpaceVer: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbBorderRadius: $me_atom2_prop_either(['.isTouch'],
      () => 5,
      () => 3,
    ),
    iconScale: $me_atom2_prop_either(['.isTouch'],
      () => .7,
      () => .7 * 24 / 32,
    ),
    iconMarginLeft: $me_atom2_prop_either(['.isTouch'],
      () => 6,
      () => 4,
    ),
    iconMarginRight: $me_atom2_prop_either(['.isTouch'],
      () => 6,
      () => 4,
    ),
    // crossMargin: $me_atom2_prop_either(['.isTouch'],
    //   () => 22 + 1,
    //   () => 16,
    // ),
    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),

    data: () => <object[]>[],

    crumbs: $me_atom2_prop<$nl_stations_crumbs_type>(['.data', '/.guid2point'],
      ({masters: [data, guid2point]}) => {
        const result = [] as $nl_stations_crumbs_type
        for (const obj of data) {
          const guid = obj.guid.toLowerCase()
          const wa = (obj.walking_access) ? obj.walking_access : 0
          const ta = (obj.transport_access) ? obj.transport_access : 0

          let t = (ta != 0) ? ', ' + ta + ' мин.' : ''
          if (!t) {
            t = (wa != 0) ? ', ' + wa + ' мин.' : ''
          }
          const point = guid2point[guid]
          if (!point || !point.station) {
            console.warn(`unknown guid ${guid}`)
          } else {
            const caption = (point && point.station) ? point.station.text + t : ''
            const isT = (ta != 0)
            const isW = (wa != 0)

            result.push(<$nl_stations_crumb_intf>{
              guid: guid,
              code: point.station.code,
              text: caption,
              color: point.line.color,
              walking_access: isW,
              transport_access: isT,
            })
          }
        }
        return result
      },
    ),
  },
}


