const row_height = 30
const row_space = 16

const prop_common = (def: any, p?: {ofsVer?: number}) => ({
  '#hidden': def.hidden,
  row: def.row,
  '#ofsVer': $me_atom2_prop(['.row', '<.row_height', '<.row_space'],
    ({masters: [row, row_height, row_space]}) =>
      8 + row * (row_height + row_space) + (p && p.ofsVer || 0),
  ),
  '#height': '<.row_height',
  col_space: def.col_space || (() => 16),
  col_count:  def.col_count || (() => 1),
  col_span:  def.col_span || (() => 1),
  col: def.col,
  '#width': !def.col_count ? '<.row_width' : $me_atom2_prop(['<.row_width', '.col_space', '.col_count', '.col_span'],
    ({masters: [width, col_space, col_count, col_span]}) =>
      Math.round((width - col_space * (col_count - 1)) / col_count) * col_span + col_space * (col_span - 1)
  ),
  '#ofsHor': !def.col_count || !def.col ? '<.row_left' : $me_atom2_prop(['<.row_left', '<.row_width', '.col_space', '.col_count', '.col'],
    ({masters: [left, width, col_space, col_count, col]}) =>
      left + col * (Math.round((width - col_space * (col_count - 1)) / col_count) + col_space)
  ),
})

//crumb_metro(result, params, 'СхемаМетро')

function crumb_input(
  result: any,
  params: any,
  fld_name: string,
  fld_caption = '',
) {
  if (params[fld_name]) {
    const caption = (!fld_caption ? '' : fld_caption + ': ') + params[fld_name]
    result[fld_name] = { caption }
  }
}

function crumb_metro(
  result: any,
  params: any,
  fld_name: string,
  fld_caption = '',
) {
  let size: number
  if (params[fld_name] && (size = params[fld_name].length)) {
    const guid = params[fld_name][0]
    const guid2point = a<any>('/.guid2point')
    const caption =
      (!fld_caption ? '' : fld_caption + ': ') +
      guid2point[guid].station.text +
      (size == 1 ? '' : ' и ещё ' + (size - 1))
    result[fld_name] = { caption }
  }
}

function crumb_pickermulti(
  result: any,
  params: any,
  fld_name: string,
  fld_caption = '',
) {
  let size: number
  if (params[fld_name] && (size = params[fld_name].size)) {
    const id = [...params[fld_name]].map(([value]) => value)[0]
    const caption =
      (!fld_caption ? '' : fld_caption + ': ') +
      $me_option_caption_text(id, $nl_advcard_panel_param_options[fld_name]) +
      (size == 1 ? '' : ' и ещё ' + (size - 1))
    result[fld_name] = { caption }
  }
}

function crumb_picker(
  result: any,
  params: any,
  fld_name: string,
  fld_caption = '',
) {
  let size: number
  console.log(fld_name, params[fld_name], $nl_advcard_panel_param_options[fld_name], Object.keys($nl_advcard_panel_param_options[fld_name]).indexOf(params[fld_name]))
  if (params[fld_name] && params[fld_name] != null && $nl_advcard_panel_param_options[fld_name] && Object.keys($nl_advcard_panel_param_options[fld_name]).indexOf(params[fld_name] + '') >=0) {
    const caption =
      (!fld_caption ? '' : fld_caption + ': ') +
        $nl_advcard_panel_param_options[fld_name][params[fld_name]].caption
    result[fld_name] = { caption }
  }
}

function crumb_select(
  result: any,
  params: any,
  fld_name: string,
  values: string[],
  captions: string[],
) {
  if (values.length < 2) $me_throw('values.length < 2', values)
  if (values.length != captions.length) $me_throw('values.length != captions.length', values, captions)
  if (params[fld_name] && params[fld_name] != values[0]) {
    let caption = captions[captions.length - 1]
    for (let i = 1; i < values.length; i++)
      if (params[fld_name] == values[i]) {
        caption = captions[i - 1]
        break
      }
    result[fld_name] = { caption }
  }
}

function crumb_diap(
  result: any,
  params: any,
  fld_name: string,
  suffix: string,
  fld_caption = '',
) {
  if (params[fld_name] && (params[fld_name].min || params[fld_name].max)) {
    if (suffix) suffix = ' ' + suffix
    const caption =
      (!fld_caption ? '' : fld_caption + ': ') +
      (
        params[fld_name].min && params[fld_name].max ? (
          params[fld_name].min == params[fld_name].max ?
            params[fld_name].min + suffix :
            params[fld_name].min + '-' + params[fld_name].max + suffix
        ) :
        params[fld_name].min ?
          'от ' + params[fld_name].min  + suffix:
          'до ' + params[fld_name].min  + suffix
      )
    result[fld_name] = { caption }
  }
}

