export interface $nl_scheme_metro_crumb_intf {
  guid: string,
  code: string,
  text: string,
  color: string,
  navText?: string,
  navFrom?: number,
  ctxLeft?: number,
  ctxTop?: number,
  ctxWidth?: number,
  ctxLeftHor?: number,
  deleteProgress?: number,
  deleteStart?: number
}

export type $nl_scheme_metro_crumbs_type = Array<$nl_scheme_metro_crumb_intf>

export const $nl_scheme_metro = <$me_atom2_elem_cnf_intf>{
  base: $nl_scheme_engine,
  prop: {
    data: '/.scheme_metro',
    //background_station: () => 'white',

    background_station: $me_atom2_prop<string>(['.theme'],
      ({masters: [theme]}) => theme == $nl_theme.light ? 'white' : '#464f63',
    ),
    background_scheme: $me_atom2_prop<string>(['.theme'],
      ({masters: [theme]}) => theme == $nl_theme.light ? 'white' : '#464f63',
    ),
    background_text_fill_style: $me_atom2_prop<string>(['.theme'],
      ({masters: [theme]}) => theme == $nl_theme.light ? 'rgba(255,255,255,.5)' : '#464f63' //'rgba(0,0,0,.5)',
    ),

    background_station_selected: () => 'red',
    background_station_will_select: () => '#F8CFD3',
    background_station_will_deselect: () => '#3F88DE',
    colorText_will_select: () => '#D5483E',
    colorText_will_deselect: '.background_station_will_deselect',

    value: () => <string[]>[],

    // value: $me_atom2_prop<string[]>(['.selected', '/.code2guids'],
    //   ({masters: [selected, code2guids]}) => {
    //     const result = [] as string[]
    //     for (const code of [...selected].map(([code]) => code)) {
    //       const guids = code2guids[code]
    //       if (guids) result.push(...guids)
    //     }
    //     return result
    //   },
    //   ({val}) => {


    //     const guid2point = a<any>('/.guid2point')
    //     const selected = new Map<string, null>()
    //     for (const guid of val) {
    //       const point = guid2point[guid]
    //       if (!point) console.warn(`unknown guid ${guid}`)
    //       if (!selected.has(point.station.code))
    //       selected.set(point.station.code, null)
    //     }
    //     console.log('apply selected', selected)

    //     a('.selected', selected)
    //   },
    // ),
    crumbs: $me_atom2_prop<$nl_scheme_metro_crumbs_type>(['.value', '/.guid2point'],
      ({masters: [value, guid2point]}) => {
        const result = [] as $nl_scheme_metro_crumbs_type
        for (const guid of value) {
          const point = guid2point[guid]
          if (!point) console.warn(`unknown guid ${guid}`)
          result.push(<$nl_scheme_metro_crumb_intf>{
            guid: guid,
            code: point.station.code,
            text: point.station.text,
            color: point.line.color,
          })
        }
        return result
      },
    ),

    // isTouch: '/.#isTouch',
    isTouch: $me_atom2_prop_store<boolean>({
      default: () => a('/.#isTouch'),
      // default: () => true,
      valid: (val: any) => typeof val == 'boolean' ? val : null,
    }),
    crumb_height: $me_atom2_prop_either(['.isTouch'],
      () => 32,
      () => 24,
    ),
    fontSize: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 14,
    ),
    crumbSpaceHor: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbSpaceVer: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 8,
    ),
    crumbBorderRadius: $me_atom2_prop_either(['.isTouch'],
      () => 5,
      () => 3,
    ),
    iconScale: $me_atom2_prop_either(['.isTouch'],
      () => .7,
      () => .7 * 24 / 32,
    ),
    iconMarginLeft: $me_atom2_prop_either(['.isTouch'],
      () => 6,
      () => 4,
    ),
    iconMarginRight: $me_atom2_prop_either(['.isTouch'],
      () => 6,
      () => 4,
    ),
    crossMargin: $me_atom2_prop_either(['.isTouch'],
      () => 22 + 1,
      () => 16,
    ),

    '#order': () => ['container', 'container2', 'lasso', 'crumbs', 'cross'],
  },

  elem: {
    crumbs: $me_atom2_prop<$me_atom2_elem_cnf_intf>(['.isTouch'],
      ({masters: [isTouch]}) => <$me_atom2_elem_cnf_intf>{
        base: $nl_scheme_metro_crumbs,
        prop: {
          '#height': $me_atom2_prop(['<.#height', '<.ofsVer_initial', '.curtainHeight'],
            ({masters: [height, ofsVer_initial, curtainHeight]}) => .25*height + ofsVer_initial + curtainHeight,
          ),
          crumbs: '<.crumbs',
          crumb_height: '<.crumb_height',
          fontSize: '<.fontSize',
          crumbSpaceHor: '<.crumbSpaceHor',
          crumbSpaceVer: '<.crumbSpaceVer',
          crumbBorderRadius: '<.crumbBorderRadius',
          iconScale: '<.iconScale',
          iconMarginLeft: '<.iconMarginLeft',
          iconMarginRight: '<.iconMarginRight',
          crossMargin: '<.crossMargin',
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 1,
          ),
        },

      },
    ),
  },
  init() {
    // console.warn('INIT')
    const worker = a<Worker>('/.metroWorker')
    if (!worker.onmessage) {
      const store_key = 'msk-metro-guids'
      worker.postMessage({cmd: 'prepare', guids: localStorage.getItem(store_key)})
      worker.onmessage = (event: MessageEvent) => {
        const cmd = event.data.cmd
        const data = event.data.data
        if (cmd == 'data') {
          a('/.scheme_metro', data.scheme)
          a('/.guid2point', data.guid2point)
          a('/.code2guids', data.code2guids)
          if (data.guids)
            localStorage.setItem(store_key, data.guids)
        }
      }
    }
    // worker.postMessage({cmd: 'prepare', guids: localStorage.getItem(store_key)})
    // .addEventListener('message', a<WorkerMessageEventListener>('.workerListener'))
  },
}

const root = $me_atom2_entity.root() as $me_atom2_entity
// root.props({
// })
root.props({
  'metroWorker': () => {
    const idx = window.location.pathname.indexOf('/', 1)
    const root = !~idx ?
      window.location.pathname :
      window.location.pathname.slice(0, idx)
    const worker = new Worker(window.location.origin + root + '/metro/-/web.js')
    // const store_key = 'msk-metro-guids'
    // worker.postMessage({cmd: 'prepare', guids: localStorage.getItem(store_key)})
    // worker.onmessage = (event: MessageEvent) => {
    //   const cmd = event.data.cmd
    //   const data = event.data.data
    //   if (cmd == 'data') {
    //     a('/.scheme_metro', data.scheme)
    //     a('/.guid2point', data.guid2point)
    //     a('/.code2guids', data.code2guids)
    //     if (data.guids)
    //       localStorage.setItem(store_key, data.guids)
    //   }
    // }
    return worker
  },
  'scheme_metro': () => null as any,
  'guid2point': () => null as any,
  'code2guids': () => null as any,
})


