// реализация timeout для fetch
export function $nl_agate_fetch(
  url: string,
  options: any,
  timeout = agateTimeout
) {
    return Promise.race([
        fetch(url, options),
        new Promise((_, reject) =>
            setTimeout(() => reject(new Error('Нет связи с сервером')), timeout)
        )
    ]).then((response: any) => {
      return (response) ? response.json() : ''
    })
    ;
}

