/*
  // * Аутентификация в Agate по телефону и токену
  // * @param {String} username Телефон пользователя
  // * @param {String} verificationToken Токен
  // * @return {Promise} .
  loginWithToken(username, verificationToken, { metric, checkMultiSession = false } = {}) {
    let body = {
      username: username,
      verificationToken: verificationToken,
      metric
    };
    if (checkMultiSession) {
      body.checkMultiSession = checkMultiSession
    }
    this.partnerCode && (body.partnerCode = this.partnerCode);

    return this._sendRequest({
      path: `${this._usersPath}/loginWithToken`,
      method: 'POST',
      body: body,
      timeout: this.timeout,
      skipRelogin: true
    })
      .then(({ response }) => this._clearLoginResponseFromForeignContacts(response))
      .catch(request => this._onFailedAgateRequest(request));
  }
*/
export function $nl_agate_login_with_token(
  username: string,
  verificationToken: string,
  metric: Object,
  checkMultiSession = false,
  partnerCode?: string
): Promise<any> {

  let body = {
    username: username,
    verificationToken: verificationToken,
    metric
  };
  if (checkMultiSession) {
    body['checkMultiSession'] = checkMultiSession
  }
  if (a<string>('.partnerCode')) {
    body['partnerCode'] = a<string>('.partnerCode')
  }

  return $nl_agate_fetch(
    `${agateUrl}/${agateUsersPath}/loginWithToken`,
    {
      method: 'POST',
      headers: agateHeaders,
      body: JSON.stringify(body),
      mode: 'cors',
   // cache: 'no-store',
    },
    agateTimeout,
    //skipLocked: true
  )//.then(({ response }) => $nl_agate_clear_login_response_from_foreign_contacts(response))
}

