export function $nl_agate_login_with_password(
  username: string,
  password: string,
  metric: Object,
  checkMultiSession = false
): Promise<any> {

  let body = {
    username: username,
    password: password,
    checkMultiSession: checkMultiSession,
    partnerCode: (a<string>('/.partnerCode')) ? a<string>('/.partnerCode') : '',
    metric: metric
  }
  // if (checkMultiSession) {
  //   body.checkMultiSession = checkMultiSession
  // }
  // if (a<string>('/.partnerCode')) {
  //   body.partnerCode = a<string>('/.partnerCode')
  // }

  return $nl_agate_fetch(
    `${agateUrl}/${agateUsersPath}/login`,
    {
      method: 'POST',
      headers: agateHeaders,
      body: JSON.stringify({ username: username, password: password }),
      mode: 'cors',
   // cache: 'no-store',
    },
    agateTimeout,
    // skipRelogin: true,
    // skipLocked: true,
    // preventAlert: true


  )//.then(({ response }) => $nl_agate_clear_login_response_from_foreign_contacts(response))
}
