export function $nl_agate_find_by_token(
  findToken: string,
  accessToken: string
): Promise<any> {

  (agateHeaders.has('Authorization')) ? agateHeaders.set('Authorization', accessToken) : agateHeaders.append('Authorization', accessToken)

  return $nl_agate_fetch(
    `${agateUrl}/${agateUsersPath}/findByToken?token=` + findToken,
    {
      method: 'GET',
      headers: agateHeaders,
      mode: 'cors',
   // cache: 'no-store',
    },
    agateTimeout,
  )
}

