namespace $.$$ {
// ============================================================================

export const $nl_checkbox = <$me_atom2_elem_cnf_intf>{
  type: '$nl_checkbox',
  base: $me_stylesheet,
  prop: {
  // required:
    caption: $me_atom2_prop_abstract<string>(),
    checked: $me_atom2_prop_abstract<boolean>(),

  // optional:
    boxSize: () => 16,
    space: () => 8,
    fontSize: $me_atom2_prop(['.boxSize'],
      $me_atom2_prop_compute_fn_sum(-2),
    ),
    colorText: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) => theme == $nl_theme.light ? '#313745' : '#ffffff',
    ),
    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),
    '#cursor': () => 'pointer',

  // internal:
    '#height': '.boxSize',

    styleSheetName: () => 'nl_checkbox',
    styleSheetCommon: $me_atom2_prop(['.styleSheetName', '/.theme', '/.colorButton', '/.colorText'],
      ({masters: [className, theme, colorButton, colorText]}) => `
        .${className} {
          user-select: none;
        }
        .${className}:hover > svg {
          transform: scale(1.2)
        }
        .${className}[checked=false] > svg {
          ${
            theme != $nl_theme.light ? '' : `border: solid 1px ${colorText};`
          }
          background-color: white;
          box-sizing: border-box;
        }
        .${className}[checked=true] > svg {
          background: ${theme == $nl_theme.light ? '#0070a4' : 'white'};
        }
        .${className}[checked=false] > svg > path {
          fill: transparent;
        }
        .${className}[checked=true] > svg > path {
          fill: ${ theme == $nl_theme.light ? 'white' : 'black' };
        }
        .${className} > div {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      `
    ),
    styleSheet: $me_atom2_prop(['.className', '.boxSize', '.colorText'],
      ({masters: [className, boxSize, colorText]}) => `
        .${className} > svg {
          border-radius: ${Math.round(boxSize * 2 / 14)}px;
        }
        .${className} > div {
          color: colorText;
        }
      `
    ),
  },
  event: {
    clickOrTap: () => {
      a<boolean>('.checked', !a<boolean>('.checked'))
      return true
    },
  },
  attr: {
    checked: '.checked',
  },
  elem: {
    box: () => <$me_atom2_elem_cnf_intf>{
      base: $me_svg,
      prop: {
        '#width': '<.boxSize',
        '#height': '<.boxSize',
        '#alignVer': () => $me_align.center,
        viewBox: () => "0 0 24 24",
        content: () => [
          {
            attr: {
              d: () => "M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z"
            },
          },
        ],
      },
    },
    caption: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#ofsHor': $me_atom2_prop(['<@box.#width', '<.space'],
          $me_atom2_prop_compute_fn_sum(),
        ),
        '#width': $me_atom2_prop(['<.#width', '.#ofsHor'],
          $me_atom2_prop_compute_fn_diff(),
        ),
        '#height': () => null as number,
        '#alignVer': () => $me_align.center,
        fontSize: '<.fontSize',
      },
      dom: {
        innerHTML: '<.caption',
      },
    },
  },
}

// ==========================================================================
}
