namespace $.$$ {
// ============================================================================

export const $nl_login_tabs = <$me_atom2_elem_cnf_intf>{
  prop: {
  // required:
    selected: $me_atom2_prop_abstract<string>(),
  // internal:
    def: () => ({
      enter: {
        condition: (selected: string) => selected == 'enter' || selected == 'register',
        prop: {
          '#width': () => 180,
          caption: () => 'ВХОД',
        },
      },
      register: {
        condition: (selected: string) => selected == 'enter' || selected == 'register',
        prop: {
          '#width': $me_atom2_prop(['<.#width', '<@tab[enter].#width'],
            ({masters: [width, tab1_width]}) => width - tab1_width,
           ),
          '#ofsHor': '<@tab[enter].#width',
          caption: () => 'РЕГИСТРАЦИЯ',
        },
      },
      restore: {
        condition: (selected: string) => selected == 'restore',
        prop: {
          caption: () => 'ВОССТАНОВЛЕНИЕ ПАРОЛЯ',
        },
      },
      confirmation: {
        condition: (selected: string) => selected == 'confirmation',
        prop: {
          caption: () => 'ПОДТВЕРЖДЕНИЕ',
        },
      },
      change: {
        condition: (selected: string) => selected == 'change',
        prop: {
          caption: () => 'СМЕНА ПАРОЛЯ',
        },
      },
      contact: {
        condition: (selected: string) => selected == 'contact',
        prop: {
          caption: () => 'ДОПОЛНИТЕЛЬНЫЙ КОНТАКТ',
        },
      },

      change_success: {
        condition: (selected: string) => selected == 'change_success',
        prop: {
          caption: () => 'СМЕНА ПАРОЛЯ ЗАВЕРШЕНА',
        },
      },

      register_success: {
        condition: (selected: string) => selected == 'register_success',
        prop: {
          caption: () => 'РЕГИСТРАЦИЯ ЗАВЕРШЕНА',
        },
      },



    }),
    tab_ids: $me_atom2_prop_keys(['.def']),
  },
  elem:  {
    tab: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.tab_ids'], masters: ['.selected', '.def']},
      ({key: [id], masters: [selected, def]}) =>
        !def[id].condition(selected) ? null :
          <$me_atom2_elem_cnf_intf>{
            prop: {
              ...def[id].prop,
              isSelected: $me_atom2_prop<boolean>(['<.selected'],
                ({masters: [selected]}) => selected == id
              ),
              '#cursor': $me_atom2_prop<string>(['.isSelected'],
                ({masters: [isSelected]}) => isSelected ? 'default' : 'pointer',
              ),
            },
            event: {
              clickOrTap: () => {
                a<string>('<.selected', id)
                return true
              },
            },
            elem: {
              caption: () => <$me_atom2_elem_cnf_intf>{
                prop: {
                  '#width': () => null as number,
                  '#alignHor': () => $me_align.center,
                },
                style: {
                  fontSize: () => 24,
                },
                dom: {
                  innerText: '<.caption',
                }
              }
            },
            style: {
              borderBottom: $me_atom2_prop(['.isSelected', '/.theme'],
                ({masters: [isSelected, theme]}) =>
                  `3px solid rgba(${
                    theme == $nl_theme.light ? '49,55,69' : '255,255,255'
                  }, ${
                    !isSelected ? .2 : theme == $nl_theme.light ? 1 : .5
                  })`,
              ),
              fontWeight: $me_atom2_prop(['.isSelected'],
                ({masters: [isSelected]}) => isSelected ? 500 : 400,
              )
            },
          },
    ),
  }
}

// ============================================================================
}