namespace $.$$ {
// ============================================================================

export const $nl_select = <$me_atom2_elem_cnf_intf>{
  base: $me_select,
  prop: {
    options: $me_atom2_prop_abstract<$me_options>(),

    value: $me_atom2_prop(['.option_ids'],
      ({masters: [ids]}) => ids[0],
    ),
    // value: $me_atom2_prop_abstract<string|Set<string>>(),

    colorBorder: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $nl_theme.light ?
        '#bdc3d1' :
        '#d8dce3'
    ),
    // () => '#bdc3d1',
    colorBorderSelected: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $nl_theme.light ?
        '#008ecf' :
        '#008ecf'
    ),
    // () => '#008ecf',
    colorBackground: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $nl_theme.light ?
        'white' :
        '#878f9b'
    ),
    colorBackgroundSelected: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $nl_theme.light ?
        '#f0f1f4' :
        '#747B89'
        // 'rgba(70,79,99,0.3)'
    ),
    // () => '#f0f1f4',
    borderRadius: () => 4,
    borderWidth: () => 1,
    paddingHor: () => 0,

    option_ids: $me_atom2_prop_keys(['.options']),
    '#width': () => 440,
    '#height': () => 32,
    option_width_min: () => 40,
    colorText: $me_atom2_prop(['/.theme', '/.colorText'],
      ({masters: [theme, colorText]}) =>
        theme == $nl_theme.light ?
        '#0070a4' :
        colorText
    ),
    // () => '#0070a4',
    colorTextSelected: '/.colorText',
    fontFamily: '/.fontFamily',
    fontWeight: '/.fontWeight',
    fontWeightSelected: '.fontWeight',
    fontSize: $me_atom2_prop(['.em'],
      $me_atom2_prop_compute_fn_mul(14 / 16),
    ),
  },
}

// ==========================================================================
}
