namespace $.$$ {
// ============================================================================

const events = [
  'focus',
  'blur',
  'change',
  'keypress',
  'keydown',
  'keyup',
]

export const $nl_input = <$me_atom2_elem_cnf_intf>{
  base: $me_stylesheet,
  node: 'input',
  dispatch: (dispatch_name: string, dispatch_arg: any) => {
    if (~events.indexOf(dispatch_name)) return true
    return false
  },
  prop: {
    styleSheetName: () => 'nl_input',
    className: '.styleSheetName',
    styleSheet: () => '',
    styleSheetCommon: $me_atom2_prop(['.className', '/.theme'],
      ({masters: [className, theme]}) => {
        return (`
          .${className}::placeholder {
            color: ${ theme == $nl_theme.light ? 'rgba(49,55,69,0.5)' : 'white' };
          }
          .${className} {
            border: solid 1px ${ theme == $nl_theme.light ? '#bdc3d1' : '#d8dce3' };
            background: ${ theme == $nl_theme.light ? '#fcfcfd' : '#666f7f' }
          }
          .${className}:focus {
            outline: none;
            border: 1px solid ${ theme == $nl_theme.light ? '#313745' : 'white' };
          }
        `)
      }
    ),
    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),
    isFocused: $me_atom2_prop([],
      () => false,
      ({val}) => {
        const node = ((a.curr as $me_atom2).parent() as $me_atom2_elem).node
        node[val ? 'focus' : 'blur']()
      },
    ),
    fontSize: $me_atom2_prop(['.em'],
      $me_atom2_prop_compute_fn_mul(14 / 16)
    ),
  },
  style: {
    borderRadius: () => 3,
    fontSize: '.fontSize',
    paddingLeft: () => 8,
    boxSizing: () => 'border-box',
    '-webkit-appearance': () => 'none',
  },
  attr: {
    placeholder: '.placeholder',
    value: '.value',
  },
  event: {
    clickOrTap: () => {
      a('.isFocused', a.dispatch('', 'focus', {result: true}).result)
      return true
    },
    clickOrTapOutside: () => {
      a('.isFocused', !a.dispatch('', 'blur', {result: true}).result)
      return false
    },
  },
  init: (self) => {
    const elem = self as $me_atom2_elem
    elem.node.addEventListener('change', onChange.bind(self))
    elem.node.addEventListener('keypress', onKeyPress.bind(self))
    elem.node.addEventListener('keydown', onKeyDown.bind(self))
    elem.node.addEventListener('keyup', onKeyUp.bind(self))
  },
  fini: (self) => {
    const elem = self as $me_atom2_elem
    elem.node.removeEventListener('change', (self as any).onChange)
    elem.node.removeEventListener('keypress', (self as any).onKeyPress)
    elem.node.removeEventListener('keydown', (self as any).onKeyDown)
    elem.node.removeEventListener('keyup', (self as any).onKeyUp)
  },
}

function onKeyEvent(entity: $me_atom2_entity, name: string, event: KeyboardEvent) {
  const prev = a.curr
  a.curr = entity
  const {ctrlKey, metaKey, shiftKey, altKey, key, type, defaultPrevented} = event
  if (a.dispatch('', name, {ctrlKey, metaKey, shiftKey, altKey, key, type, defaultPrevented}).defaultPrevented)
    event.preventDefault()
  a.curr = prev
}

function onKeyUp(event: KeyboardEvent) {
  onKeyEvent(this as $me_atom2_entity, 'keyup', event)
}

function onKeyDown(event: KeyboardEvent) {
  onKeyEvent(this as $me_atom2_entity, 'keydown', event)
}

function onKeyPress(event: KeyboardEvent) {
  onKeyEvent(this as $me_atom2_entity, 'keypress', event)
}

function onChange(event: Event) {
  const prev = a.curr
  a.curr = this
  a.dispatch('', 'change', (event.target as any).value)
  a.curr = prev
}

// ==========================================================================
}
