const header = <$me_atom2_elem_cnf_intf>{
  prop: {
    '#height': '<.header_height',
    arrow: () => ['left', 'right'],
  },
  style: {
    background: '<.header_background_color',
  },
  elem: {
    arrow: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.arrow'], masters: ['<.mode', '<.value']},
      ({key: [id], masters: [mode, value]}) =>
      id == 'right' && (() => {
        const [month_new, year_new] = new_month_year(value, mode)
        let val = new Date(year_new, month_new, 1)
        const result = !a.dispatch('<', 'isValid', {val, result: false}).result
        return result
      })() ? null :
      <$me_atom2_elem_cnf_intf>{
        prop: {
          '#alignHor': () => $me_align[id],
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 1,
          ),
          '#width': $me_atom2_prop(['<<.arrow_size', '<<.arrow_ofsHor'],
            ({masters: [size, ofsHor]}) => size + 2 * ofsHor,
          ),
          '#cursor': () => 'pointer',
        },
        elem: {
          triangle: () => <$me_atom2_elem_cnf_intf>{
            base: $me_triangle,
            prop: {
              '#alignVer': () => $me_align.center,
              '#ofsHor': '<<<.arrow_ofsHor',
              color: '<<<.header_text_color',
              direction: () => $me_pos_enum[id],
              size: '<<<.arrow_size',
            },
          },
        },
        event: {
          clickOrTap: () => {
            const value = a<Date>('<<.value')
            const [month_new, year_new] = new_month_year(
              value,
              a<$nl_calendar_mode>('<<.mode'),
              a('@triangle.direction') - 1
            )
            const day = value.getDate()
            let day_new = Math.min(day, month_day_count([month_new, year_new]))
            let val = new Date(year_new, month_new, day_new)
            while (!a.dispatch('<<', 'isValid', {val, result: false}).result)
              val = new Date(year_new, month_new, --day_new)
            a<Date>('<<.value', val)
            return true
          },
        },
      },
    ),
    caption: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#ofsHor': $me_atom2_prop(['<<.arrow_ofsHor', '<<.arrow_size'],
          ({masters: [arrow_ofsHor, arrow_width]}) => 2 * arrow_ofsHor + arrow_width
        ),
        '#width': $me_atom2_prop(['<.#width', '.#ofsHor'],
          ({masters: [width, ofsHor]}) => width - 2 * ofsHor
        ),
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
        '#cursor': () => 'pointer',
      },
      event: {
        clickOrTap: () => {
          a<$nl_calendar_mode>('<<.mode',
            a<$nl_calendar_mode>('<<.mode') == $nl_calendar_mode.month ?
              $nl_calendar_mode.day :
              $nl_calendar_mode.month
          )
          return true
        },
      },
      elem: {
        label: () => <$me_atom2_elem_cnf_intf>{
          prop: {
            '#width': () => null as number,
            '#height': () => null as number,
            '#align': () => $me_align.center,
          },
          dom: {
            innerText: $me_atom2_prop<string, [Date, string[]]>(
              $me_atom2_prop_masters(['<<<.mode'],
                ({masters: [mode]}) => {
                  const result = ['<<<.value']
                  if (mode == $nl_calendar_mode.day) result.push('<<<.months')
                  return result
                }
              ),
              ({len, masters: [value, months]}) =>
                (len < 2 ? '' : months[value.getMonth()] + ' ') + value.getFullYear(),
            ),
          },
          style: {
            color: '<<<.header_text_color',
            fontSize: $me_atom2_prop(['<<<.k', '/.em'],
              ({masters: [k, em]}) => Math.round(k * em)
            ),
          },
        },
      },
    },
  },
}

