const content_day = <$me_atom2_elem_cnf_intf>{
  prop: {
    day_size: '<.day_size',
    '#height': $me_atom2_prop(['@header.#height', '@content.#height'],
      $me_atom2_prop_compute_fn_sum(),
    ),
    '#ofsVer': '<@header.#height',
    weekdays: '<.weekdays',
    weekday_left: $me_atom2_prop(
      {
        keys: ['.weekdays'],
        masters: $me_atom2_prop_masters(['.weekdays'],
          ({key: [weekday], masters: [weekdays]}) => {
            const idx = weekdays.indexOf(weekday)
            return !idx ? [] : [`.weekday_left[${weekdays[idx - 1]}]`, `.day_size`]
          }
        ),
      },
      $me_atom2_prop_compute_fn_sum(),
    ),
    days: $me_atom2_prop<string[], [Date]>(['<.value'],
      ({masters: [value]}) => {
        const weekday = (value.getDay() + 6) % 7
        const dayOfMonth = value.getDate() - 1
        const i_min = - (weekday + 7 - dayOfMonth % 7) % 7
        let i_max = dayOfMonth + 6 - weekday
        const day_count = month_day_count(value)
        while (i_max < day_count - 1) i_max += 7
        const result = [] as string[]
        for (let i = i_min; i <= i_max; i++) {
          result.push(i + '')
        }
        return result
      },
    ),
    day_caption: $me_atom2_prop<number, [Date]>({keys: ['.days'], masters: ['<.value']},
      ({key: [day], masters: [value]}) => {
        let result = +day + 1
        if (result <= 0) {
          result = month_day_count( [(value.getMonth() + 11) % 12, value.getFullYear() - (value.getMonth() ? 0 : 1) ] ) + result
        } else {
          const day_count = month_day_count(value)
          if (result > day_count)
            result = result - day_count
        }
        return result
      },
    ),
    day_top: $me_atom2_prop<number, [string[], number]>({keys: ['.days'], masters: ['.days', '.day_size']},
      ({key: [day], masters: [days, day_size]}) => {
        return Math.floor(days.indexOf(day) / 7) * day_size
      },
    ),
    day_left: $me_atom2_prop<number, [string[], number]>({keys: ['.days'], masters: ['.days', '.day_size']},
      ({key: [day], masters: [days, day_size]}) => {
        return days.indexOf(day) % 7 * day_size
      },
    ),
  },
  elem: {
    header: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': '<<.content_day_header_height',
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
        '#cursor': () => 'default',
      },
      style: {
        background: '<<.content_header_background_color',
      },
      elem: {
        weekday: $me_atom2_prop({keys: ['<.weekdays']},
          ({key: [weekday]}) => <$me_atom2_elem_cnf_intf>{
            prop: {
              '#width': `<<.day_size`,
              '#ofsHor': `<<.weekday_left[${weekday}]`,
            },
            elem: {
              label: () => <$me_atom2_elem_cnf_intf>{
                prop: {
                  '#width': () => null as number,
                  '#height': () => null as number,
                  '#align': () => $me_align.center,
                },
                style: {
                  fontSize: $me_atom2_prop(['<<<<.k', '/.em'],
                    ({masters: [k, em]}) => Math.round(k * em)
                  ),
                  color: '<<<<.content_header_text_color',
                },
                dom: {
                  innerText: () => weekday,
                },
              },
            },
          },
        ),
      },
    },
    content: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#ofsVer': '<@header.#height',
        '#height': $me_atom2_prop(
          $me_atom2_prop_masters(['<.days'],
            ({masters: [days]}) => [`<.day_top[${days[days.length - 1]}]`, '<.day_size'],
          ),
          $me_atom2_prop_compute_fn_sum(),
        ),
      },
      elem: {
        day: $me_atom2_prop({keys: ['<.days']},
          ({key: [day]}) => <$me_atom2_elem_cnf_intf>{
            prop: {
              '#width': `<<.day_size`,
              '#height': `<<.day_size`,
              '#ofsHor': `<<.day_left[${day}]`,
              '#ofsVer': `<<.day_top[${day}]`,
              isSelected: $me_atom2_prop<boolean, [Date]>(['<<<.value'],
                ({masters: [value]}) => value.getDate() == +day + 1
              ),
              isAvail: $me_atom2_prop<boolean, [Date]>(['<<<.value'],
                ({masters: [value]}) => +day >= 0 && +day < month_day_count(value),
              ),
              '#zIndex': $me_atom2_prop(['<.#zIndex'],
                ({masters: [zIndex]}) => zIndex + 1,
              ),
              '#cursor': $me_atom2_prop(['.isAvail', '.isValid'],
                ({masters: [isAvail, isValid]}) => !(isAvail && isValid) ? null : 'pointer',
              ),
              isValid: $me_atom2_prop(['<<<.value'],
                ({masters: [value]}) => {
                  const value_new = new Date(value.getFullYear(), value.getMonth(), +day + 1)
                  const result = a.dispatch('<<<', 'isValid', {val: value_new, result: false}).result
                  return result
                }
              ),
              content_selected_background_color: '<<<.content_selected_background_color',
              content_selected_background_border: '<<<.content_selected_background_border',
            },
            style: {
              ...style_selected,
            },
            event: {
              clickOrTap: () => {
                // if (!(a<boolean>('.isValid') && a<boolean>('.isAvail'))) return false
                if (!a<string>('.#cursor')) return false
                if (!a<boolean>('.isSelected')) {
                  const value = a<Date>('<<<.value')
                  const value_new = new Date(value.getFullYear(), value.getMonth(), +day + 1)
                  a<Date>('<<<.value', value_new)
                }
                a.dispatch('<<<', 'didSelect')
                return true
              },
            },
            elem: {
              label: $me_atom2_prop(['.isAvail'],
                ({masters: [isAvail]}) => !isAvail ? null : <$me_atom2_elem_cnf_intf>{
                  prop: {
                    '#width': () => null as number,
                    '#height': () => null as number,
                    '#align': () => $me_align.center,
                  },
                  style: {
                    color: $me_atom2_prop(['<.isValid', '<<<<.content_text_color', '<<<<.content_invalid_text_color'],
                      ({masters: [isValid, validColor, invalidColor]}) => isValid ? validColor : invalidColor,
                    ),
                    fontSize: $me_atom2_prop(['<<<<.k', '/.em'],
                      ({masters: [k, em]}) => Math.round(k * em)
                    ),
                  },
                  dom: {
                    innerText: `<<<.day_caption[${day}]`,
                  },
                },
              ),
            },
          },
        ),
      },
    },
  },
}

