const style_selected = {
  background: $me_atom2_prop<string>(['.isSelected', '.content_selected_background_color'],
    ({masters: [isSelected, color]}) => isSelected ? color : 'transparent',
  ),
  border: $me_atom2_prop<string>(['.isSelected', '.content_selected_background_border'],
    ({masters: [isSelected, border]}) => isSelected ? border : 'none',
  ),
  boxSizing: () => 'border-box',
}

// ============================================================================

const month_day_count = (value: Date | [number, number]) => {
  const [month, year] =
    value instanceof Date ?
      [value.getMonth(), value.getFullYear()] :
      value as [number, number]
  const result = month != 1 ?
    [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31][month] :
  year % 4 == 0 && year % 100 != 0 || year % 400 == 0 ? // https://habr.com/ru/post/164521/
    29 :
    28
  return result
}

