namespace $.$$ {
// ============================================================================

export const $nl_subscription_tabs = <$me_atom2_elem_cnf_intf>{
  prop: {
    options: $me_atom2_prop_abstract<$me_options>(),
    selected: $me_atom2_prop_abstract<string>(),
    option_ids: $me_atom2_prop_keys(['.options']),
  },
  elem: {
    tab: $me_atom2_prop({keys: ['.option_ids']},
      ({key: [id]}) => <$me_atom2_elem_cnf_intf>{
        base: tab,
        prop: {
          id: () => id,
        },
        dom: {
          innerText: $me_atom2_prop(['<.options'],
            ({masters: [options]}) => $me_option_caption_text(id, options).toUpperCase(),
          ),
          // `<<.section_title[${id}]`,
        },
      },
    ),
  },
}

const tab = <$me_atom2_elem_cnf_intf>{
  node: 'span',
  prop: {
    isSelected: $me_atom2_prop(['<.selected', '.id'],
      ({masters: [selected, id]}) => selected == id
    ),
    '#cursor': $me_atom2_prop(['.isSelected'],
      ({masters: [isSelected]}) => isSelected ? 'default' : 'pointer',
    ),
  },
  event: {
    clickOrTap: () => {
      console.log(a('.id'), a('<.selected'), (a.get('<.selected') as $me_atom2).name())
      a('<.selected', a('.id'))
      return true
    },
  },
  style: {
    position: () => 'relative',
    paddingLeft: () => 10,
    paddingRight: () => 10,
    paddingBottom: () => 5,
    borderBottom: $me_atom2_prop(['.isSelected', '/.theme'],
      ({masters: [isSelected, theme]}) =>
        `3px solid rgba(${
          theme == $nl_theme.light ? '49,55,69' : '255,255,255'
        }, ${
          !isSelected ? .2 : theme == $nl_theme.light ? 1 : .5
        })`,
    ),
    fontSize: $me_atom2_prop(['.em'],
      $me_atom2_prop_compute_fn_mul(18 / 16),
    ),
    fontWeight: $me_atom2_prop(['.isSelected'],
      ({masters: [isSelected]}) => isSelected ? 500 : 400,
    )
  },
}

// ==========================================================================
}