namespace $.$$ {
// ============================================================================

export const $nl_advs_workspace = <$me_atom2_elem_cnf_intf>{
  prop: {
    orders: () => ({
      'new': {
        id: 'new',
      },
      'id2': {
        id: 'id2',
        title: 'ПРОДАЖА КВАРТИР В МОСКВЕ',
        // result_mode: 'ТАБЛИЦА',
        // result_mode: 'ПЛИТКА',
        params: {
          // rmqt: new Set(['1', '2']),
        },
      },
/*
      'id1': {
        id: 'id1',
        title: 'ПРОДАЖА КОМНАТ В МОСКВЕ',
        // result_mode: 'ТАБЛИЦА',
        params: {
          // rmqt: new Set(['4', '6+']),
        },
      },
*/
    }),
    order_ids: $me_atom2_prop_keys(['.orders']),
    order: $me_atom2_prop(
      {keys: ['.order_ids'], masters: ['.orders']},
      ({key: [id], masters: [orders]}) => orders[id],
    ),
    order_title: $me_atom2_prop(
      {keys: ['.order_ids'], masters: ['.order[]']},
      ({masters: [order]}) => order.title.toUpperCase(),
    ),
    selected: $me_atom2_prop_store({
      default: () => a<string[]>('.order_ids')[0],
      valid: (val) => val == 'new' || a<string[]>('.orders')[val] ? val : null,
    }),
    param_modes: () => ({
      ПОЛНЫЙ: {
        height: 8 * 44,
      },
      СЖАТЫЙ: {
        height: 0,
      },
    }),
    // $me_atom2_prop(['.alt'],
    //   ({masters: [alt]}) => ({
    //     ПОЛНЫЙ: {
    //       height: alt ? 518 : 630,
    //     },
    //     СЖАТЫЙ: {
    //       height: 120,
    //     },
    //   })
    // ),
    param_mode_keys: $me_atom2_prop_keys(['.param_modes']),
    param_mode: $me_atom2_prop_store({
      default: () => 'ПОЛНЫЙ',
      valid: (val) => ~a<string>('.param_mode_keys').indexOf(val) ? val : null,
    }),
    offerCount: () => 1200,
    objCount: () => 800,
    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),
  },
  style: {
    // background: $me_atom2_prop(['/.theme'],
    //   ({masters: [theme]}) => theme == $nl_theme.light ? '#D9DCE2' : '#8C93A4',
    // ),
    // border: () => '1px solid red',
    overflow: () =>  'hidden',
  },



  elem: {
    tabs: () => <$me_atom2_elem_cnf_intf>{
      base: $nl_advs_tabs,
      prop: {
        '#height': '/@app@menu@login.#height',
        '#ofsHor': () => 36,
        '#ofsVer': () => 16,
        '#width': () => 600
      },
    },

    new: $me_atom2_prop<$me_atom2_elem_cnf_intf, [string]>(['.selected'],
      ({masters: [selected], prev}) => selected != 'new' ? prev || null : <$me_atom2_elem_cnf_intf>{
        // TODO: avoid manual type, implement auto type in elem.ts
        type: '$nl_advs_new', // required for identity (for cache purpose: see elem.ts:144)
        base: $nl_advs_new,
        prop: {
          '#hidden': $me_atom2_prop<boolean>(['<.selected'],
            ({masters: [selected]}) => selected != 'new',
          ),
          '#ofsVer': '<@tabs.#height',
          '#height': $me_atom2_prop(['<.#height', '<@tabs.#height'],
            ({masters: [height_parent, height_tabs]}) => height_parent - height_tabs
          ),
        },
      },
    ),

    panelResult: $me_atom2_prop<$me_atom2_elem_cnf_intf, [string]>(['.selected'],
      ({masters: [selected], prev}) => selected == 'new' ? prev || null : <$me_atom2_elem_cnf_intf>{
        type: '$nl_advs_panel_result',// required for identity (for cache purpose: see elem.ts:144)
        base: $nl_advs_panel_result,
        prop: {
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 3,
          ),
          '#hidden': $me_atom2_prop(['<.selected'],
            ({masters: [selected]}) => selected == 'new',
          ),
          order: $me_atom2_prop<any>(['<.selected'],
            ({masters: [selected]}) => selected == 'new' ? null : a(`<.order[${selected}]`)
          ),
          mode: $me_atom2_prop<string>(['<.selected'],
            ({masters: [selected]}) => selected == 'new' ? '' : a<any>(`<.order[${selected}]`).result_mode || 'АКТИВНЫЕ',
            ({val}) => {
              if (!val) return
              const order = a<any>(`.order`)
              order.result_mode = val
              a<any>(`.order`, order, true)
            },
          ),

          '#ofsHor': $me_atom2_prop(['.isZoomed', '.em'],
            ({masters: [isZoomed, em]}) => isZoomed ? 0 : em,
          ),
          ofsVer_target: '<@tabs.#height',
          '#ofsVer': $me_atom2_prop(
            $me_atom2_prop_masters(['.isZoomed'],
              ({masters: [isZoomed]}) => isZoomed ? [] : ['.ofsVer_target'],
            ),
            ({len, masters: [to], prev}) => !len ? 0 : !prev ? to : $me_atom2_anim({to, duration: 400}),
          ),


          height_target: $me_atom2_prop(
            $me_atom2_prop_masters(['.isZoomed'],
              ({masters: [isZoomed]}) => isZoomed ? ['/.#viewportHeight', '.em'] : ['<.#height', '.ofsVer_target', '.em'],
            ),
            ({len, masters}) => {
              if (len == 2) {
                const [viewportHeight, em] = masters
                return viewportHeight - em
              } else {
                const [height, ofsVer, em] = masters
                return height - ofsVer - em
              }
            },
          ),
          '#height': $me_atom2_prop(
            $me_atom2_prop_masters(['.isZoomed'],
              ({masters: [isZoomed]}) => isZoomed ?
                ['.isZoomed', '/.#viewportHeight'] :
                ['.isZoomed', '/.#viewportHeight', '.height_target']
            ),
            ({masters: [isZoomed, viewportHeight, to], prev}) =>
              isZoomed ?
                viewportHeight :
              prev == viewportHeight ?
                to :
                $me_atom2_anim({to, duration: 400,
                  path_active: (a.get('.height_anim_is') as $me_atom2).path
                }),
          ),
          height_anim_is: $me_atom2_prop([],
            () => false,
          ),
          '#width': $me_atom2_prop(['.isZoomed', '/.#viewportWidth', '<.#width', '.em'],
            ({masters: [isZoomed, viewportWidth, width, em]}) =>
              isZoomed ? viewportWidth : width - 3 * em
          ),
        },
        style: {
          overflow: ()=> 'hidden',
        }
      },
    ),

  },
}

// ==========================================================================
}