namespace $.$$ {
// ============================================================================

export type $me_equal_type<Value> = (a: Value, b: Value) => boolean
export function $me_equal(a: any, b: any) : boolean {
  let result: boolean
  if (a === b) {
    result = true
  } else if (a && b && typeof a == 'object' && typeof b == 'object') {
    const isArrayA = isArray(a), isArrayB = isArray(b)
    const isSetA = a instanceof Set, isSetB = b instanceof Set
    const isMapA = a instanceof Map, isMapB = b instanceof Map
    const isDateA = a instanceof Date, isDateB = b instanceof Date
    const isRegexpA = a instanceof RegExp, isRegexpB = b instanceof RegExp
    const isUint8ArrayA = a instanceof Uint8Array, isUint8ArrayB = b instanceof Uint8Array
    result =
      isArrayA === isArrayB && isSetA === isSetB && isMapA === isMapB && isDateA == isDateB && (
        isArrayA && isArrayB ? eqArrHelper(a, b) :
        isSetA && isSetB ? a.size == b.size && eqSetHelper(a, b) && eqSetHelper(b, a) :
        isMapA && isMapB ? $me_equal([...a], [...b]) :
        isDateA && isDateB ? a.getTime() == b.getTime() :
        isRegexpA && isRegexpB ? a.toString() == b.toString() :
        isUint8ArrayA && isUint8ArrayB ? eqBufHelper(a, b) :
        eqObjHelper(a, b)
      )
  } else {
    result = a!==a && b!==b
  }
  // if ($me_debug) console.warn({result})
  return result
}

// https://github.com/epoberezkin/fast-deep-$me_equal/blob/master/index.js
const isArray = Array.isArray
const keyList = Object.keys
const hasProp = Object.prototype.hasOwnProperty

function eqBufHelper(a: Uint8Array, b: Uint8Array) {
  let result = a.byteLength == b.byteLength
  for (let i = 0; result && i < a.byteLength; i++)
    result = a[i] == b[i]
  return result
}

function eqSetHelper(a: Set<any>, b: Set<any>) {
  let result = true
  const iter = a.keys()
  let next = iter.next()
  while (!next.done && (result = b.has(next.value))) next = iter.next()
  return result
}

function eqArrHelper(a: Array<any>, b: Array<any>) {
  const length = a.length
  let result = length == b.length
  for (let i = 0; result && i < length; i++) {
    result = $me_equal(a[i], b[i])
    // if ($me_debug) console.log({result}, a[i], b[i])
  }
  // console.log({result})
  return result
}

function eqObjHelper(a: any, b: any) {
  let result = true
  const keys = keyList(a)
  const length = keys.length
  result = length == keyList(b).length
  for (let i = 0; result && i < length; i++)
    result = hasProp.call(b, keys[i])
  for (let i = 0; result && i < length; i++) {
    const key = keys[i]
    result = $me_equal(a[key], b[key])
  }
  return result
}

// ==========================================================================
}
