namespace $.$$ {
// ============================================================================

export const $nl_scheme_selector = <$me_atom2_elem_cnf_intf>{

  prop: {
    '#height': ()=> 30,
    items: () => <$me_options>{
      metro: {
        icon: 'assets/icons-8-marker.png',
        caption: 'Метро',
      },
      railway: {
        icon: 'assets/icons-8-marker.png',
        caption: 'Ж/Д',
      },
      msk: {
        icon: 'assets/icons-8-marker.png',
        caption: 'Районы',
      },
      mo: {
        icon: 'assets/icons-8-marker.png',
        caption: 'Подмосковье',
      },
      map: {
        icon: 'assets/icons-8-marker.png',
        caption: 'Карта',
      },
    },
    icon_width: () => 12,
    icon_height: () => 18,
    icon_margin_right: () => 8,

    item_space: () => 20,
    fontSize: () => 14,

    item_ids: $me_atom2_prop_keys(['.items']),
    item: $me_atom2_prop(
      {keys: ['.item_ids'], masters: ['.items']},
      ({key: [id], masters: [items]}) => items[id],
    ),
    item_width: $me_atom2_prop(
      {
        keys: ['.item_ids'],
        masters: $me_atom2_prop_masters([],
          ({key: [id]}) => [`@tab[${id}].#width`]
        ),
      },
      $me_atom2_prop_compute_fn_sum(),
      ({key: [id], val}) => console.log(id, val)
    ),
    item_left: $me_atom2_prop(
      {
        keys: ['.item_ids'],
        masters: $me_atom2_prop_masters(['.item_ids'],
          ({key: [id], masters: [item_ids]}) => {
            const idx = item_ids.indexOf(id)
            if (!idx) return []
            id = item_ids[idx - 1]
            return [`.item_left[${id}]`, `.item_width[${id}]`, '.item_space']
          }
        ),
      },
      $me_atom2_prop_compute_fn_sum(),
    ),
  },

  elem: {
    separator: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.item_ids'], masters: ['.item_ids']},
      ({key: [id], masters: [item_ids]}) => !item_ids.indexOf(id) ? null : <$me_atom2_elem_cnf_intf>{
        prop: {
          colorText: () => '#9699a1',
          '#height': () => null as number,
          '#width': () => null as number,
          '#alignVer': () => $me_align.center,
          '#ofsHor': $me_atom2_prop([`<.item_left[${id}]`, '<.item_space'],
            ({masters: [left, space]}) => left - (Math.round(space/2)) - 2,
          ),
          fontSize: $me_atom2_prop([`<.fontSize`],
            ({masters: [size]}) => size + 2,
          ),
        },
        dom: {
          innerText: () => '/',
        },
      },
    ),
    tab: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.item_ids']},
      ({key: [id]}) => <$me_atom2_elem_cnf_intf>{


        dispatch(dispatch_name: string, dispatch_arg: any) {
          if (dispatch_name == 'close_scheme') {

            console.log('eeeeeeeeee');
            a('.isShown', false)
            return true
          }
        },

        prop: {
          id: () => id,
          isShown: () => false,
          '#ofsHor': `<.item_left[${id}]`,
          '#height': '<.#height',
          '#width': $me_atom2_prop(['@icon.#width', '@text.#width', '<.icon_margin_right'],
            $me_atom2_prop_compute_fn_sum()
          ),
          '#cursor': () => 'pointer',
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 1,
          ),

        },
        elem: {

          icon: () => <$me_atom2_elem_cnf_intf>{
            node: 'img',
            prop: {
              '#width': '<<.icon_width',
              '#height': '<<.icon_height',
              '#ofsHor': () => 0,//'.pm',
              '#alignVer': () => $me_align.center,
            },
            attr: {
              src: $me_atom2_prop(['<<.items'],
                ({masters: [items]}) => items[id].icon,
              ),
            },
            style: {
              filter: $me_atom2_prop<string>(['/.theme'],
                ({masters: [theme]}) => theme == $nl_theme.light ? '' : 'brightness(0%) invert(100%) sepia(89%) saturate(0%) hue-rotate(253deg) brightness(112%) contrast(100%)'
              ),
            },
          },
          text: () => <$me_atom2_elem_cnf_intf>{
            prop: {
              '#height': () => null as number,
              '#width': () => null as number,
              '#alignVer': () => $me_align.center,
              '#alignHor': () => $me_align.right,
              fontSize: '<<.fontSize',
              //colorText: '/.colorLink',
              // '#ofsHor': $me_atom2_prop(['<@icon.#width', '<@icon.#ofsHor', '.pm'],
              //   ({masters: [width, ofs, ofs2]}) => width + ofs + ofs2 + 20,
              // ),
            },
            dom: {
              innerText: $me_atom2_prop(['<<.items'],
                ({masters: [items]}) => $me_option_caption_text(id, items),
              ),
            },
            style:{
              whiteSpace: () => 'nowrap',
              color: $me_atom2_prop(['/.theme'],
                ({masters: [theme]}) => theme == $nl_theme.light ? '#0070a4' : 'white',
              ),
            }
          },
          scheme: $me_atom2_prop<$me_atom2_elem_cnf_intf>(['.isShown', '.id'],
            ({masters: [isShown, id]}) => <boolean>(!isShown) ? null : {
              base: $nl_scheme_mo,
              prop: {
                isShown: $me_atom2_prop_bind('<.isShown'),
                //login: $me_atom2_prop_bind('<.login'),
              },
            }
          ),
        },
        event: {
          clickOrTap: () => {
            console.log('clicked on ', a('.id'));
            a.update<boolean>('.isShown', val => !val)
            return true
          },
  // event: {
  //   clickOrTap: () => {
  //     if (!a<string>('/@app.login')) {
  //       a('/@app.isShownLoginForm', true)
  //     } else {
  //       a.update<boolean>('/@app.isShownLoginMenu', val => !val)
  //     }
  //     return true
  //   },
  // },


        },
      },
    ),
  },

}

// ==========================================================================
}