namespace $.$$ {
// ============================================================================

function today() {
  const now = new Date()
  return new Date(now.getFullYear(), now.getMonth(), now.getDate())
}

function asString(val: number) {
  const result =
    val == Infinity ? 'За все время' :
    val == 0 ? 'Сегодня' :
    val + ' ' + $me_word_plural(val, 'день', 'дня', 'дней')
  return result
}

export const $nl_pickerdate = <$me_atom2_elem_cnf_intf>{
  prop: {
    value: () => 6,

    theme: '/.theme',
    input_width: $me_atom2_prop(['.value'],
      ({masters: [value]}) => value == Infinity ? 150 : 90,
    ),
    buttonDropdown_width: () => 40,
    text_margin: () => 3, //16,

    icon_size: $me_atom2_prop(['.#height'],
      //$me_atom2_prop_compute_fn_mul(28/40)
      $me_atom2_prop_compute_fn_mul(28/32)
    ),
    icon_filter: $me_atom2_prop<string>(['.theme'],
      ({masters: [theme]}) => theme == $nl_theme.light ?
        // black => #5E98BA  by codepen.io/sosuke/pen/Pjoqqp
        'invert(65%) sepia(17%) saturate(1025%) hue-rotate(158deg) brightness(84%) contrast(87%)' :
        // black => #438FC4 by codepen.io/sosuke/pen/Pjoqqp
        'invert(46%) sepia(87%) saturate(371%) hue-rotate(162deg) brightness(93%) contrast(84%)'
    ),
    isTouch: '/.#isTouch',
  },
  style: {
    userSelect: () => 'none',
  },
  elem: {
    input: () => <$me_atom2_elem_cnf_intf>{
      base: $nl_input,
      dispatch(dispatch_name: string, dispatch_arg: any) {
        if (dispatch_name == 'change') {
          a('<.value', dispatch_arg == '' ? Infinity : Number.parseInt(dispatch_arg, 10))
          return true
        }
        return false
      },
      prop: {
        '#width': '<.input_width',
        fontSize: () => 14,
      },
      dom: {
        value: $me_atom2_prop<string, [number, boolean]>(['<.value', '.isFocused'],
          ({masters: [value, isFocused]}) => {
            const result = value
            return (
              !isFocused ? asString(result) :
              result == Infinity ? '' :
              result + ''
            )
          }
        ),
      },
    },
    buttonDropdown: () => <$me_atom2_elem_cnf_intf>{
      base: $nl_picker,
      prop: {
        '#ofsHor': $me_atom2_prop(['<.input_width', '<.buttonDropdown_width'],
          $me_atom2_prop_compute_fn_diff(),
        ),
        '#width': '<.buttonDropdown_width',
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 2,
        ),
        '#cursor': () => 'pointer',
        options: $me_atom2_prop(['<.value'],
          ({masters: [value]}) => {
            const vals = [0, 1, 2, 3, 7, 14, 30, 50, 120, 180, Infinity]
            const idx = vals.findIndex((val: number) => value >= val)
            if (vals[idx] != value) vals.splice(idx, 0, value)
            const result = {} as any
            for (const val of vals) {
              result[val + ''] = { caption: asString(val) }
            }
            return result
          },
        ),
        value: $me_atom2_prop(['<.value'],
          ({masters: [value]}) => value + '',
          ({val}) => {
            a('<.value', val == (Infinity + '') ? Infinity : +val)
          },
        ),
        dropdownTarget: () => '<@input',
        isTouch: '<.isTouch',
      },
      style: {
        background: () => 'transparent',
        border: () => 'none',
      },
      event: {
        clickOrTap: () => {
          a<boolean>('<@input.isFocused', false)
          console.log(a<boolean>('<@input.isFocused'))
          return true
        },
      },
      elem: {
        triangle: () => <$me_atom2_elem_cnf_intf>{
          base: $nl_triangle,
          prop: {
            '#ofsVer': () => 2,
          },
        },
        text: () => null as $me_atom2_elem_cnf_intf,
      },
    },
    text: $me_atom2_prop(['.value'],
      ({masters: [value]}) => value == Infinity ? null :
      <$me_atom2_elem_cnf_intf>{
        prop: {
          '#ofsHor': $me_atom2_prop(['<.input_width', '<.text_margin'],
            $me_atom2_prop_compute_fn_sum(),
          ),
          '#width': $me_atom2_prop(['<.#width', '.#ofsHor', '<.text_margin', '<@buttonCalendar.#width'],
            $me_atom2_prop_compute_fn_diff(),
          ),
        },
        style: {
          whiteSpace: () => 'nowrap',
          overflow: () => 'hidden',
          textOverflow: () => 'ellipsis',
        },
        elem: {
          label: () => <$me_atom2_elem_cnf_intf>{
            prop: {
              '#width': () => null as number,
              '#height': () => null as number,
              '#align': () => $me_align.center,
            },
            style: {
              fontSize: $me_atom2_prop(['.em'],
                $me_atom2_prop_compute_fn_mul(14 / 16)
              ),
            },
            dom: {
              innerText: $me_atom2_prop<string, [number]>(['<<.value'],
                ({masters: [value]}) => {
                  const dt = value == Infinity ? null : new Date(today().getTime() - value * 86400000)
                  const result =
                    'с ' +
                    dt.getDate() +
                    ' ' +
                    ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня', 'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря'][dt.getMonth()] +
                    ' ' +
                    dt.getFullYear()
                  return result
                }
              ),
            },
          },
        },
      },
    ),
    buttonCalendar: $me_atom2_prop(['.value'],
      ({masters: [value]}) => value == Infinity ? null :
      <$me_atom2_elem_cnf_intf>{
        base: $me_dropdown,
        dispatch: (dispatch_name: string, dispatch_arg: any) => {
          if (dispatch_name == 'dropdown') {
            const {prop_isDropdown, prop_clientRect, isTouch} = dispatch_arg
            const prop_value = a.get('<.value') as $me_atom2
            dispatch_arg.result = <$me_atom2_elem_cnf_intf>{
              base: $nl_calendar,
              dispatch: (dispatch_name: string, dispatch_arg: any) => {
                if (dispatch_name == 'didSelect') {
                  prop_isDropdown.value(false)
                  return true
                }
                return false
              },
              prop: {
                value: $me_atom2_prop<Date>([prop_value.name()],
                  ({masters: [value]}) =>
                    value == Infinity ? null : new Date(today().getTime() - value * 86400000),
                  ({val}) => {
                    const value = Math.ceil((today().getTime() - val.getTime()) / 86400000)
                    prop_value.value(value)
                  },
                ),
                k: () => isTouch ? 2 : 1,
                '#ofsHor': !isTouch ?
                  $me_atom2_prop([prop_clientRect.name(), '.#width'],
                    ({masters: [clientRect, width]}) =>
                      clientRect.right - width,
                  ) :
                  $me_atom2_prop(['.#width', '/.#viewportWidth'],
                    ({masters: [width, viewportWidth]}) => (viewportWidth - width) / 2
                  ),
                marginVer: () => 4,
                '#ofsVer': !isTouch ?
                  $me_atom2_prop([prop_clientRect.name(), '/.#viewportHeight', '.#height', '.marginVer'],
                    ({masters: [clientRect, viewportHeight, height, marginVer]}) =>
                      clientRect.bottom + marginVer + height <= viewportHeight ?
                        clientRect.bottom + marginVer :
                        Math.max(0, viewportHeight - height - marginVer)
                  ) :
                  $me_atom2_prop(['.#height', '/.#viewportHeight'],
                    ({masters: [width, viewportWidth]}) => (viewportWidth - width) / 2
                  ),
              },
            }
            return true
          }
          return false
        },
        node: 'img',
        prop: {
          '#height': '<.icon_size',
          '#width': '<.icon_size',
          '#alignVer': () => $me_align.center,
          '#alignHor': () => $me_align.right,
          '#cursor': () => 'pointer',
          isTouch: '<.isTouch',
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 1,
          ),
        },
        style: {
          filter: '<.icon_filter',
        },
        attr: {
          src: () => 'assets/icons-8-today@2x.png'
        },
      },
    ),
  },
}

// ==========================================================================
}
