const news_item = <$me_atom2_elem_cnf_intf>{
  prop: {
    '#height': () => 100,
    caption: $me_atom2_prop_abstract<string>(),
    imageUrl: $me_atom2_prop_abstract<string>(),
    text: $me_atom2_prop_abstract<string>(),
    url: $me_atom2_prop_abstract<string>(),
    date: $me_atom2_prop_abstract<string>(),
  },
  elem: {
    image: () => <$me_atom2_elem_cnf_intf>{
      node: 'img',
      prop: {
        '#width': () => 130,
        '#height': () => 97,
        '#ofsHor': () => 0,
        '#ofsVer': () => 0,
      },
      attr: {
        src: '<.imageUrl',
      },
    },

    title: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': () => null as number,
        '#width': () => null as number,
        '#ofsVer': '<@icon1.#ofsVer',
        '#ofsHor': $me_atom2_prop(['<@image.#ofsHor', '<@image.#width', '.em'],
          ({masters: [ofs_h, width, ofs]}) => ofs_h + width + ofs,
        ),
      },
      style: {
        fontFamily: () => 'system-ui',
        fontSize: () => 12,
        fontWeight: () => 500,
      },
      dom: {
        innerText: '<.caption',
      },
    },

    text: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': () => null as number,
        '#width': $me_atom2_prop(['<.#width', '<@image.#width', '.em'],
          ({masters: [width, img_width, ofs]}) => width - img_width - ofs,
        ),
        '#ofsVer': $me_atom2_prop(['<@image.#ofsVer', '<@title.#height'],
          ({masters: [ofs, height]}) => ofs + height + 10,
        ),
        '#ofsHor': $me_atom2_prop(['<@image.#ofsHor', '<@image.#width', '.em'],
          ({masters: [ofs_h, width, ofs]}) => ofs_h + width + ofs,
        ),
      },
      style: {
        fontFamily: () => 'system-ui',
        fontSize: () => 12,
        fontWeight: () => 400,
      },
      dom: {
        innerText: '<.text',
      },
    },

    more: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => null as number,
        '#ofsVer': $me_atom2_prop(['<@image.#ofsVer'],
          ({masters: [ofs]}) => ofs + 86,
        ),

        '#ofsHor': $me_atom2_prop(['<@image.#ofsHor', '<@image.#width', '.em'],
          ({masters: [ofs_h, width, ofs]}) => ofs_h + width + ofs,
        ),
        '#cursor': () => 'pointer',
        colorText: '/.colorLink',
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
        fontSize: () => 12,
      },
      dom: {
        innerText: () => 'Подробнее'
      },
      event: {
        clickOrTap: () => {
          console.log('link');
          return true
        },
      },
    },

    date: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => null as number,
        '#ofsVer': $me_atom2_prop(['<@image.#ofsVer'],
          ({masters: [ofs]}) => ofs + 86,
        ),
        '#alignHor': () => $me_align.right,
        fontSize: () => 10,
      },
      dom: {
        innerText: '<.date'
      },
      style: {
        color: $me_atom2_prop(['/.theme'],
          ({masters: [theme]}) =>
            theme == $nl_theme.light ? '#6a6c74' : '#d0d0d0',
        ),
      },
    },
  }
}

