export function $nl_metric_item_event(
  itemGuid: string,
  objectGuid: string,
  offerId: string,
  kind: string
): Promise<any> {

  return fetch(
    `${metricUrl}/api/v1/itemEvents`,
    {
      method: 'POST',
      //headers: metricHeaders,
      mode: 'cors',
      body: JSON.stringify({
        kind: kind,
        appCode: metricAppCode,
        itemGuid: itemGuid,
        objectGuid: objectGuid,
        offerId: offerId,
        userId: get_userId,
        userGuid: get_userGuid,
      }),
   // cache: 'no-store',
    },
  )
}



