namespace $.$$ {
// ============================================================================

export const $nl_advcard = <$me_atom2_elem_cnf_intf>{
  prop: {
    '#width': '/.#viewportWidth',
    '#height': '/.#viewportHeight',
    // '#zIndex': $me_atom2_prop(['<.#zIndex'],
    //   ({masters: [zIndex]}) => zIndex + 1,
    // ),



    isTouch: $me_atom2_prop_store({
      default: () => a('/.#isTouch'),
      valid: (val: any) => typeof val == 'boolean' ? val : null,
    }),
  },

  elem: {
    background: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': '<.#height',
        '#width': '<.#width',
        // '#zIndex': $me_atom2_prop(['<.#zIndex'],
        //   ({masters: [zIndex]}) => zIndex + 1,
        // ),
      },
      style: {
        background: () => 'rgba(0, 0, 0, .4)',
      },
    },

    wrapper: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        //'#height': () => 560,// '<.#height',
        '#width': () => 700,
        '#height': $me_atom2_prop(['@panelParam.#height', '@buttonPanel.#height'],
          ({masters: [h1, h2]}) => h1 + h2,
        ),

        '#alignHor': () => $me_align.center,
        '#alignVer': () => $me_align.center,
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
        grayColor: $me_atom2_prop(['/.theme'],
          ({masters: [theme]}) =>
            theme == $nl_theme.light ? '#979aa1' : '#f1f1f1',
        ),
      },
      style: {
        background: $me_atom2_prop(['/.theme'],
          ({masters: [theme]}) =>
            theme == $nl_theme.light ? 'white' : '#414c5f',
        ),
      },

      elem: {
        cross: () => <$me_atom2_elem_cnf_intf>{
          base: $me_cross,
          prop: {
            size: () => 24,
            thick: () => 3,
            '#ofsVer': () => 10,
            '#ofsHor': () => 12,
            '#alignHor': () => $me_align.right,
            color: '<.grayColor',
            '#zIndex': $me_atom2_prop(['<.#zIndex'],
              ({masters: [zIndex]}) => zIndex + 2,
            ),
            '#cursor': () => 'pointer',
          },

          event: {
            clickOrTap: () => {
              a<boolean>('/@app.isShownAdvCard', false)
              return true
            },
          },
        },

        panelParam: () => <$me_atom2_elem_cnf_intf>{
            //type: '$nl_advcard_panel_param', // required for identity (for cache purpose: see elem.ts:144)
            base: $nl_advcard_panel_param,
            prop: {
              param_mode: () => 'ПОЛНЫЙ',
              value: $me_atom2_prop_bind('/@app.card_value'),

              // order: $me_atom2_prop(['<.selected'],
              //   ({masters: [selected]}) => selected == 'new' ? null : a(`<.order[${selected}]`),
              //   ({val}) => {
              //     a(`<.order[${a('<.selected')}]`, val, true)
              //   },
              // ),
              //'#height': () => 500,
            },

        },

        buttonPanel: () => <$me_atom2_elem_cnf_intf>{
          prop: {
            buttonHeight: () => 30,
            verticalOffset: () => 15,
            '#ofsVer': $me_atom2_prop(['<@panelParam.#ofsVer', '<@panelParam.#height'],
              ({masters: [ofs, height]}) => ofs + height,
            ),
            '#ofsHor': () => 0,
            '#height': () => 60,
          },
          elem: {
            saveButton: () => <$me_atom2_elem_cnf_intf>{
              base: $nl_button,
              prop: {
                '#width': () => 87,
                '#height': '<.buttonHeight',
                '#ofsVer': '<.verticalOffset',
                '#ofsHor': () => 160,
                caption: () => 'Сохранить',
                target: () => '<',
                fontSize: () => 14,
              },
              event: {
                clickOrTap: () => {
                  a<boolean>('/@app.isShownAdvCard', false)
                  return true
                },
              }
            },
            saveAndPubButton: () => <$me_atom2_elem_cnf_intf>{
              base: $nl_button,
              prop: {
                '#width': () => 190,
                '#height': '<.buttonHeight',
                '#ofsVer': '<.verticalOffset',
                '#ofsHor': $me_atom2_prop(['<@saveButton.#ofsHor', '<@saveButton.#width'],
                  ({masters: [ofs, width]}) => ofs + width + 10,
                ),
                caption: () => 'Сохранить и опубликовать',
                target: () => '<',
                fontSize: () => 14,
              },
              event: {
                clickOrTap: () => {
                  a<boolean>('/@app.isShownAdvCard', false)
                  return true
                },
              }
            },
            cancelButton: () => <$me_atom2_elem_cnf_intf>{
              base: $nl_button,
              prop: {
                '#width': () => 87,
                '#height': '<.buttonHeight',
                '#ofsVer': '<.verticalOffset',
                '#ofsHor': $me_atom2_prop(['<@saveAndPubButton.#ofsHor', '<@saveAndPubButton.#width'],
                  ({masters: [ofs, width]}) => ofs + width + 10,
                ),
                caption: () => 'Отменить',
                target: () => '<',
                fontSize: () => 14,
              },
              event: {
                clickOrTap: () => {
                  a<boolean>('/@app.isShownAdvCard', false)
                  return true
                },
              }
            },
          }
        }


      }
    }
  },
}

// ==========================================================================
}