export function $nl_login_get_string_constant(
  sectionName: string,
  constantName: string,
  mode: string
): string {
  let result = stringConstants[sectionName][constantName];
  //if (isPlainObject(result)) {
  if (Object.prototype.toString.call(result) === '[object Object]') {
    mode = mode || a<string>('/@app@login.mode')
    result = result[mode];
  }
  return result
}


