export function $nl_check_login() {
/*
        this.agateGate.findByToken(accessToken, accessToken)
          .then(({ response }) => {
            const user = response;
            return this.agateGate.getUserContacts(user.id, accessToken)
              .then(contacts => {
                user.contacts = contacts;

                return {
                  user: user,
                  token: {
                    id: accessToken
                  } ,
                  ip: user.ip
                }
              })
          })
*/
  const accessToken = a<string>('/@app.accessToken')
  if (accessToken) {
    console.log('check access token ', accessToken)
    $nl_agate_find_by_token(accessToken, accessToken)
      .then((response: any) => {
        const user = response
        return $nl_agate_get_user_contact(user.id, accessToken)
          .then((contacts: any) => {
            return contacts//.filter(contact => contact.userId === user.id || contact.userId === null);
          })
            .then((contacts: any) => {
              user.contacts = contacts
              return {
                user: user,
                token: {
                  id: accessToken
                } ,
                ip: user.ip
              }
            })
            .then(responses => {
              console.log('responses', responses)
              $nl_success_login_helper(responses, responses.ip)
            })
            .catch(err => {
              console.log('ERROR ', err)
              if (a('/@app.isLoggedIn')) {
                $nl_logout()
//              } else {
//                this.login()
              }
            })
      })
  }
}

export function $nl_logout_helper() {
  a('/@app.isLoggedIn', false)
  //a('/@app.stayLogged', false)
  a('/@app.login', '')
  a('/@app.name', '')
  a('/@app.mlsBrokerGuid', '')
  a('/@app.accessToken', '')
  a('/@app.userId', '')
  a('/@app.userAccountCode', '')
  a('/@app.userInternalIp', '')
  a('/@app.userExternalIp', '')
  a('/@app.userRestrictions', [])
  a('/@app.userPermissionCodes', [])
  a('/@app.needSignDocDate', '')
//          needSignDocDate: _.get(userParams, 'winnerBillingInfo.needSignDocDate', null),
  a('/@app.userContacts', [])
  a('/@app.loginnedAs', '')
  a('/@app.roles', [])
  a('/@app.blackPhone', null)
  //a('.isShown', false)
}

export function $nl_success_login_helper(
  params: any,
  ip: string
) {

  a('/@app.isLoggedIn', true)
  //a('/@app.stayLogged', a('/.stayLogged'))
  a('/@app.login', params.user.username)
  a('/@app.name', params.user.name)
  a('/@app.mlsBrokerGuid', params.user.mlsBrokerGuid)
  a('/@app.accessToken', params.token.id)
  a('/@app.userId', params.user.id + '')
  a('/@app.userAccountCode', params.user.billingClientCode)
  a('/@app.userInternalIp', null)
  a('/@app.userExternalIp', ip)

  a('/@app.userRestrictions', params.user.restrictions ? params.user.restrictions : [])
  a('/@app.userPermissionCodes', params.user.permissions ? params.user.permissions.map((permission:any) => permission.code) : [])
  a('/@app.needSignDocDate', params.user.winnerBillingInfo.needSignDocDate)
//          needSignDocDate: _.get(userParams, 'winnerBillingInfo.needSignDocDate', null),
  a('/@app.userContacts', params.user.contacts || [])
  //a('/@app.loginnedAs', a('.login'))
  a('/@app.roles', params.user.roles)
  a('/@app.blackPhone', params.user.settings ? params.user.settings.blackPhone : null)
}

