const input_pass = <$me_atom2_elem_cnf_intf>{
  base: $nl_login_input,
  dispatch(dispatch_name: string, dispatch_arg: any): boolean {
    if (dispatch_name == 'icon_click') {
      a.update<boolean>('.isPassword', val => !val)
      return true
    }
    return false
  },
  prop: {
    type: $me_atom2_prop(['.isPassword'],
      ({masters: [isPassword]}) => !isPassword ? 'text' : 'password',
    ),
    icon_width: () => 25,
    icon_height: $me_atom2_prop(['.isPassword'],
      ({masters: [isPassword]}) =>
        isPassword ? 11 : 18
    ),
    icon_margin_right: () => 30,
    icon_isClickable: () => true,
    icon: $me_atom2_prop(['.isPassword'],
      ({masters: [isPassword]}) => {
        const result = isPassword ?
          $nl_icon_eyeopen :
          $nl_icon_eyeclose
        return result
      }
    ),
    isPassword: $me_atom2_prop_bind('<<.isPassword'),
    // error: () => 'Неверный пароль',
  },
}


const confirmation_message = <$me_atom2_elem_cnf_intf>{
  prop: {
    identifierKind : () => 'phone',//'<.identifierKind'//
  },
  elem: {
    msg: () =>  <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': () => null as number,
        '#width': () => null as number,
      },
      dom: {
        innerHTML: $me_atom2_prop(['<.identifierKind'],
          ({masters: [kind]}) => {
            let res = (kind == 'phone') ?
              '<ul><li>Вы правильно указали номер телефона;</li><li>Телефон включен и находится в зоне действия сети.</li></ul>' :
              '<ul name="email"><li>Вы правильно указали e-mail;</li><li>Сообщение не попало в папку "Спам".</li></ul>'

            return '<p>Пожалуйста, убедитесь, что:</p>' + res + '<p>После этого </p>'
          }
        ),
      },
    },
    link: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#height': () => null as number,
        '#width': () => null as number,
        '#cursor': () => 'pointer',
        '#ofsVer': () => 106,
        '#ofsHor': () => 100,
        colorText: '/.colorLink',
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
      },
      style: {
        'white-space': ()=> 'nowrap',
      },
      dom: {
        innerText: () => 'повторно запросите код подтверждения'
      },
      event: {
        clickOrTap: () => {
          a<boolean>('<<<._resendConfirmationVisible', false);
          a.dispatch('/@app@login', 'resend')
          return true
        },
      },
    }
  },



}






